import { Component, OnInit, TemplateRef } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
import { DataTableResource } from 'angular7-data-table';

import { SlidersService } from './sliders.service';

@Component({
  selector: 'app-sliders',
  templateUrl: './sliders.component.html',
  styleUrls: ['./sliders.component.scss'],
})
export class SlidersComponent implements OnInit {
  itemResource;
  items = [];
  itemCount = 0;
  limit = 20;
  limits = [20, 25, 50];
  offset = 1;
  tableElements = [];

  modalRef: any;

  constructor(
    private sliderService: SlidersService,
    private modalService: BsModalService,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService
  ) {}

  ngOnInit(): void {
    this.sliderService.getAll();

    this.spinner.show();

    this.sliderService.sliders.subscribe(async (data) => {
      if (data) {
        this.items = data;

        this.itemResource = new DataTableResource(data);
        this.itemCount = await this.itemResource.count();
        this.reloadItems({
          offset: 0,
          limit: this.limit,
        });
      }
    });

    this.spinner.hide();
  }

  async reloadItems(event) {
    if (this.itemResource) {
      this.tableElements = await this.itemResource.query(event);
    }
  }

  itemId;
  openModal(template: TemplateRef<any>, id) {
    this.itemId = id;
    this.modalRef = this.modalService.show(template);
  }

  deleteElement() {
    this.spinner.show();

    this.modalRef.hide();
    this.sliderService.deleteOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.sliderService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }
}
